import React from 'react';
import styled from 'styled-components';
import BannerBackground from '../../assets/Introbanner.jpg';
import Image1 from '../../assets/introbanner1.jpg'
import Image2 from '../../assets/introbanner2.jpg';
import Image3 from '../../assets/introbanner3.jpg';
import person from '../../assets/person1.png'

const Wrapper = styled.section`
  width: 100vw;
 
`;
const Introbannercontainer = styled.div`
  width: 100vw;
  height: 40vh;
  background-image: url(${BannerBackground});
  background-size: cover;
  background-position: center;
`;
const Backdropcontainer = styled.div`
  backdrop-filter: blur(14px);
  width: 100%;
  height: 100%;
  position: relative;
`;
const Textcontainer = styled.div`
  position: absolute;
  top: 10%;
  left: 12%;
  background-color: transparent;
  width: 400px;
  height: 30%;
  font-size: 25px;
  color: black;
  font-weight: 500;
  text-transform: capitalize;
  color: #000;
  margin: 0;
  .promise {
    font-weight: 700;
    font-size: 47px;
  }
  h3 {
    font-size: 24px;
    width: 1000px;
    font-weight: 400;
  }
`;

const SecondContainer = styled.div`
  max-width: 1300px;
  margin: 3rem auto;
  .description {
    width: 1000px;
    margin: 0 auto;
    font-size: 17px;
    line-height: 2rem;
  }
`
const DescriptionContainer = styled.div`
  max-width: 1300px;
  height: 60vh;
  background-image: ${({ image }) => `url(${image})`};
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 3rem auto;
  border-radius: 30px;
  padding: 77px;
  .textbox {
    h3 {
      font-size: 2.1rem;
      font-weight: 700;
      color: #fff;
    }
    span {
      font-size: 1.4rem;
      font-weight: 700;
      color: #fff;
    }
  }
`;

const Intropeoplecontainer = styled.div`
  max-width: 1300px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 3rem;
  .card{
    width: 200px;
    .imagecontainer{
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      overflow:hidden;
      img{
        width: 200px;
        height: 200px;
      }
    }
  }
  .name{
    text-align:center;
    font-size:27px;
    letter-spacing:0.14rem ;
    font-weight: 800;
  }
  .jobcontainer{
    h4{
      font-size:12px;
      font-weight: 500;
    }
  }
`;

const Peopletitile= styled.h4`
  font-size: 2rem;
  max-width: 1300px ;
  margin:2rem auto;
  text-align:center;
  
`

const IntroComponent = () => {
  return (
    <Wrapper>
      {/* intro box */}
      <Introbannercontainer>
        <Backdropcontainer>
          <Textcontainer>
            <h4> </h4>
            <span className="promise">
              Onroom<br></br>  
            </span>
            <h3>
                   .  
               .
            </h3>
          </Textcontainer>
        </Backdropcontainer>
      </Introbannercontainer>

      {/* second paragraph */}
      <SecondContainer>
        <p className="description">
                .
          , ,     ,
                 
               .   
            5    
              (Global Code of Conduct)
          ,     5  
                 .
        </p>
      </SecondContainer>
      <DescriptionContainer image={Image1}>
        <div className="textbox">
          <h3> </h3>
          <span>
                   
             ,
            <br />    .
          </span>
        </div>
      </DescriptionContainer>
      <DescriptionContainer image={Image2}>
        <div className="textbox">
          <h3> </h3>
          <span>
                   
              
            <br /> ,      .
          </span>
        </div>
      </DescriptionContainer>
      <DescriptionContainer image={Image3}>
        <div className="textbox">
          <h3>Onroom    </h3>
          <span>
                  
             ..
          </span>
        </div>
      </DescriptionContainer>
      <Peopletitile>
         
      </Peopletitile>
      <Intropeoplecontainer>
        <div className="card">
          <div className="imagecontainer">
            <img src={person} alt="person" />
          </div>
          <h3 className="name">(Terry)</h3>
          <div className="jobcontainer">
            <h4>ONROOM CO-Founder (CEO)</h4>
            <h4>Marketing Director</h4>
          </div>
        </div>
        <div className="card">
          <div className="imagecontainer">
            <img src={person} alt="person" />
          </div>
          <h3 className="name">(Eric)</h3>
          <div className="jobcontainer">
            <h4>ONROOM CO-Founder (CEO)</h4>
            <h4>Developemnt Director</h4>
          </div>
        </div>
        <div className="card">
          <div className="imagecontainer">
            <img src={person} alt="person" />
          </div>
          <h3 className="name"></h3>
          <div className="jobcontainer">
            <h4>ONROOM CO-Founder (CEO)</h4>
            <h4>Design Director</h4>
          </div>
        </div>
        <div className="card">
          <div className="imagecontainer">
            <img src={person} alt="person" />
          </div>
          <h3 className="name"></h3>
          <div className="jobcontainer">
            <h4>ONROOM CO-Founder (CEO)</h4>
            <h4>UI/UX animation Director</h4>
          </div>
        </div>
      </Intropeoplecontainer>
    </Wrapper>
  );
};

export default IntroComponent;
