import React from 'react';
import styled from 'styled-components';
import { Innerlayout } from '../../styles/layout';
import PersonCard from '../util/PersonCard';
import Primarybutton from '../util/Primarybutton';
import { CommonStyles } from '../../staticFiles/CommonStyles';
import p1 from '../../assets/profile/person1.jpeg';
import p2 from '../../assets/profile/person2.png';
import p3 from '../../assets/profile/person3.jpeg';
import p4 from '../../assets/profile/person4.jpeg';

const Ourtemadata = [
  {
    name: '',
    job: 'Creative Director',
    job2: 'CDO(Chief Design Officer)',
    image: p1,
  },
  {
    name: '',
    job: 'Marketing Director',
    job2: 'CMO(Chief Marketing Officer)',
    image: p4,
  },
  {
    name: '',
    job: 'Development Director',
    job2: 'CTO(Chief Technology Officer)',
    image: p2,
  },
  {
    name: '',
    job: 'Development Manager',
    job2: 'CFO(Chief Financial Officer)',
    image: p3,
  },
];

const Ourteam = () => {
  return (
    <Innerlayout>
      <Ourteamstyle>
        <div className="title">
          <h4 className="main">  </h4>
          <div className="line"></div>
          <p className="paragraph">
            Onroom        
            ''      , ,
                    
            ONROOM  .     ,
                    
            
          </p>
          <Primarybutton
            name=" "
            style={{ width: '200px', height: '50px' }}
          />
        </div>
        <div className="cardcontainer">
          {Ourtemadata.map((data, index) => {
            return (
              <PersonCard
                key={index}
                name={data.name}
                job={data.job}
                job2={data.job2}
                image={data.image}
              />
            );
          })}
        </div>
      </Ourteamstyle>
    </Innerlayout>
  );
};

const Ourteamstyle = styled.section`
  margin-bottom: 100px;
  .title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .line {
      background-color: ${CommonStyles.color.Primary};
      width: 130px;
      height: 4px;
      border-radius: 30px;
      margin-top: -20px;
      margin-bottom: 16px;
    }
    .main {
      font-weight: bold;
      font-size: 42px;
      line-height: 52px;
      text-align: center;
      letter-spacing: -0.4px;
      color: ${CommonStyles.color.Dark};
      margin-bottom: 20px;
    }
    .paragraph {
      font-weight: normal;
      font-size: 22px;
      line-height: 32px;
      /* or 145% */

      text-align: center;

      /* Colors / Text */
      width: 1030px;
      color: rgba(21, 20, 57, 0.4);

      mix-blend-mode: normal;
      margin-bottom: 47.2px;
    }
  }
  .cardcontainer {
    margin-top: 76.8px;
    padding: 2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    row-gap: 30px;
  }
`;

export default Ourteam;
